.gal-select{
    position:relative;
    display:flex;
    flex-direction:column;
    border:none
}
.gal-select__trigger{
    position:relative;
    display:flex;
    align-items:center;
    justify-content:space-between;
    padding:0 0;
    padding-bottom:calc(10px / 2);
    height:auto;
    background:#000;
    cursor:pointer;
    border:none;
    border-bottom:1px solid #bfc500;
    margin-bottom:calc(10px * 2)
}
.gal-options{
    overflow-y:scroll;
    scrollbar-color:#fff transparent;
    height:200px;
    position:absolute;
    display:block;
    top:100%;
    left:0;
    right:0;
    border:none;
    background:#bfc500;
    transition:all .25s;
    opacity:0;
    visibility:hidden;
    pointer-events:none;
    z-index:2;
    margin-top:-21px;
    font-size:.9em
}
.gal-select.open .gal-options{
    opacity:1;
    visibility:visible;
    pointer-events:all
}
.gal-select.set .gal-options{
    opacity:0;
    visibility:none;
    pointer-events:none
}
.gal-option{
    position:relative;
    display:block;
    padding:0 22px 0 22px;
    color:#000;
    line-height:60px;
    cursor:pointer;
    transition:all .25s;
    font-weight:bold
}
.gal-option:hover{
    cursor:pointer;
    background-color:#929600
}
.gal-option.selected{
    background-color:#929600
}
.gal-arrow{
    position:relative;
    height:10px;
    width:10px
}
.gal-arrow::before,.gal-arrow::after{
    content:"";
    position:absolute;
    bottom:0px;
    width:.1rem;
    height:100%;
    transition:all .25s
}
.gal-arrow::before{
    left:-10px;
    -webkit-transform:rotate(-45deg);
            transform:rotate(-45deg);
    background-color:#bfc500
}
.gal-arrow::after{
    left:-4px;
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg);
    background-color:#bfc500
}
.open .gal-arrow::before{
    left:-10px;
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg)
}
.open .gal-arrow::after{
    left:-4px;
    -webkit-transform:rotate(-45deg);
            transform:rotate(-45deg)
}
